@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body{
    background-color: #181818;
    font-family: 'Montserrat', sans-serif;
}
.navi-bar{
    background-color: #080808;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    border: 1px solid #ff0050;
    outline: 1px solid #00f2ea;
}
.navi-bar.footer{
    height: 100px;
    margin-top: 40px;
    align-items: center;
    justify-content: center;
}
.ul-navi{
    padding: 0;
}
.li-navi, .navi-tt-icon{
    display: inline-block;
    color: white;
    list-style-type: none !important;
    font-size: 18.5px;
}
.navi-tt-icon {
    margin-right: 5px;
    margin-left: 10px;
    font-size: 23px;
}


.search-div-title{
    margin-top: 40px;
    display: flex;
    justify-content: center;
    text-align: center;
}
.search-title{
    font-size: 40px;
    margin-bottom: -10px;
    color: #FFFDFA;
    text-shadow: -1.8px -1.8px #ff0050,
                -1.8px 1.8px #ff0050,
                1.8px -1.8px #00f2ea,
                1.8px 1.8px #ff0050;

}
.search-div{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.search-link-input{
    width: 18rem;
    height: 2.6em;
}
.search-link-input::placeholder{
    padding-left: 7px;
}
.search-link-input::-webkit-input-placeholder{
    padding-left: 7px;
}
.search-link-input::-moz-placeholder {
    padding-left: 7px;
}
.search-icon-btn{
    font-size: 1.5em;
}
.search-loading-div{
    display: flex;
    justify-content: center;
    text-align: center;
}
.search-loading-icon{
    color: #FFFDFA;
    margin-top: 40px;
    font-size: 40px;
    opacity: 0;
    transition: all 200ms;
    animation: search-rotate 1s infinite;
    animation-timing-function: ease-in-out;
}
@keyframes search-rotate {
    0% {
        transform: rotateZ(0deg);
    }
    100%{
        transform: rotateZ(360deg);
    }
}

.download-container{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 40px;
}
.download-icon{
    text-decoration: none !important;
    color: #FFFDFA;
    background-color: #202020;
    font-size: 30px;
    border: 2px solid #FFFDFA;
    width: 5em;
    height: 1.5em;
    border-radius: 5%;
    transition: all 1s;
    opacity: 1;

}

.tt-line{
    margin-top: 100px;
    display: flex;
    width: 100%;
    height: 5px;
    background-size: 400% 200%;
    background-position: 0% 100%;
    background: rgb(255,0,80);
    background: linear-gradient(169deg, rgba(255,0,80,1) 0%, rgba(0,242,234,1) 100%);

}
.tt-line {
    animation-name: line-gradient;
    animation-duration: 10s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
.tt-line.bottom{
    margin-top: 25px;
}

@keyframes line-gradient {
    0% {
        background-position: 0% 0%;
    }

    100% {
        background-position: 100vw 100rem;
    }
}



.info-container{
    display: flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.info-container.first{
    margin-top: 0px;
}
.info-infinite-icon, .info-timer-icon, .info-privacy-icon{
    margin-top: 50px;
    font-size: 150px;
    color: #FFFDFA;
}
.info-infinite-icon{
    margin-top: 0;
}
.info-timer-icon, .info-privacy-icon{
    font-size: 140px;
    margin-bottom: 20px;
}
.info-header{
    margin-top: -20px;
    color: #FFFDFA;
    text-shadow: -1.8px -1.8px #ff0050,
            -1.8px 1.8px #ff0050,
            1.8px -1.8px #00f2ea,
            1.8px 1.8px #ff0050;
}
.info-description{
    word-spacing: 1px;
    line-height: 1.3;
    color: #FFFDFA;
    font-weight: 300;
    text-align: justify;
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 15px;
}


